<template>
  <div class="btn1">
    <ul>
      <li
        v-for="(item,index) of arr"
        :key="index"
        :class="[index===0||index===2||index===4?'item':'line',curName===item?'active':'']"
        @click="handleClick(item)"
      >
        {{item}}
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "btn1",
    data() {
      return {
        arr: ["全部", "", "进京访", "", "赴省访"],
        curName: "全部"
      }
    },
    methods: {
      handleClick(name) {
        this.curName = name;
        this.$emit("change",this.curName);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .btn1
    position relative
    width 320px
    height 71px
    background url("./img/btn1-bg.png") no-repeat
    ul
      display flex
      width 90%
      margin 16px auto
      align-items center
      li
        border-bottom 2px solid transparent
        &.item
          flex 1
          line-height 40px
          color #8df6ff
          font-size 0.18rem
          text-align center
          cursor pointer
        &.line
          width 1px
          height 16px
          background-color #999ea7
        &.active
          color #fff
          font-weight 700
          border-bottom 2px solid #f3ff3a
</style>
