<template>
  <div class="btn2">
    <ul>
      <li
        v-for="(item,index) of arr"
        :key="index"
        :class="curName===item?'active':''"
        @click="handleClick(item)"
      >
        <p>{{item}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "btn2",
    data() {
      return {
        arr: ["件次", "人次"],
        curName: "件次"
      }
    },
    methods: {
      handleClick(name) {
        this.curName = name;
        this.$emit("change", this.curName);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .btn2
    position relative
    width 100px
    ul
      li
        width 100px
        border 1px solid #29adff
        cursor pointer
        margin 12px 0
        border-radius 20px
        &.active
          p
            color #fff
            font-weight 700
            border-bottom 2px solid #f3ff3a
        p
          width 80%
          margin 0 auto
          line-height 30px
          color #8df6ff
          font-size 0.18rem
          border-bottom 2px solid transparent
          text-align center

</style>
