<!--
echarts 基本饼图
@auth:lyq
-->
<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width:canvasStyle.width,height:canvasStyle.height}"
  ></div>
</template>

<script>
  import echarts from 'echarts'
  // // 引入基本模板
  // let echarts = require("echarts/lib/echarts");
  // //引入饼状图
  // require("echarts/lib/chart/pie");
  //
  // // 引入提示框和标题组件
  // require("echarts/lib/component/tooltip");
  // require("echarts/lib/component/title");
  // require("echarts/lib/component/legend");

  export default {
    name: "PieProgress",
    props: {
      params: {
        type: Object,
        default: function () {
          return {
            canvasStyle: {
              width: "500px",
              height: "500px"
            },
            options: {
              //饼图间隔
              placeHolder: true,
              //饼图高亮
              // blur: true,
              titleShow: true,
              //顺时针 默认为逆时针
              clockwise: true,
              //标签文字与百分比是否折行
              labelCut: true,
              //玫瑰图配置项，默认为空，不配置则为基本饼图
              roseType: "area", //area,radius
              //图例
              legend: {
                show: true,
                left: 10,
                top: 240,
                orient: "vertical" //horizontal垂直
              },
              // 饼图大小
              radius: ["30%", "60%"],
              // 饼图位置，默认垂直水平居中
              center: ["50%", "50%"],
              // 是否让百分比显示在扇区里面，默认false显示在外面
              insidePercent: false,
              // 计量单位
              unit: "条"
            },
            data: [
              {
                name: "A",
                value: 10
              },
              {
                name: "B",
                value: 30
              },
              {
                name: "C",
                value: 70
              }
            ]
          };
        }
      },
      animateFlag: Boolean
    },
    data() {
      return {
        myChart: null
      };
    },
    mounted() {
      this.initChart();
      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      initChart() {
        if (this.myChart) {
          this.myChart.dispose();
        }

        this.myChart = echarts.init(this.$refs.chartEl);
        /**
         * 数据处理
         */

        if (this.params.data.length === 0) return;

        let options = this.params.options;

        //注意：使用sort排序会改变原数组，因此会循环处罚watch中的 params 变化。可用复制数据。
        let chartData = [...this.params.data];

        // chartData.sort((a, b) => b.value - a.value)

        //根据总数算出每个扇区之间的间隙值
        let total = 0,
          placeHolder = 0;
        chartData.forEach(item => {
          total += item.value;
        });
        placeHolder = total * 0.015;

        let rich = {
            text: {
              color: "#fff",
              fontSize: 16,
              align: "center",
            },
            value: {
              color: "#fff",
              // color: (params) => {
              // return Array.isArray(options.colors[params.dataIndex])
              //   ?
              //   options.colors[params.dataIndex][0]
              //   :
              //   options.colors[params.dataIndex]
              // },
              fontSize: 20,
              fontWeight: "bold",
              align: "center",
              lineHeight: 20,
            },
            percent: {
              color: '#e03562',
              fontSize: options.labelInnerSize ? options.labelInnerSize : 16,
              fontWeight: "bold",
              lineHeight: 30,
              align: "center",
              padding: [3, 0]
            },
            total: {
              color: this.$STANDARD.mainColor,
              // fontSize: this.$STANDARD.h2.fontSize
            }
          },
          //饼图扇区间隔
          placeHolderStyle = {
            normal: {
              label: {
                show: false
              },
              labelLine: {
                show: false
              },
              color: "rgba(0, 0, 0, 0)",
              borderColor: "rgba(0, 0, 0, 0)",
              borderWidth: 0
            }
          },
          //处理过后的饼图数据
          fixData = [],
          //填充进饼图的数据
          fullData = {},
          //作为间隔的数据
          placeHolderData = {},
          color = options.colors;

        for (let i = 0, length = chartData.length; i < length; i++) {
          // 扇区数据
          fullData = {
            value: chartData[i].value,
            name: chartData[i].name
          };

          // 间隔数据
          placeHolderData = {
            value: placeHolder,
            name: "",
            itemStyle: placeHolderStyle
          };

          // 饼图是否有间隔,选择放入不同的数据
          options.placeHolder
            ? fixData.push(fullData, placeHolderData)
            : fixData.push(fullData);
        }

        // 数据扇区
        let seriesItem = {
            name: "外部扇区",
            hoverAnimation: false,
            type: "pie",
            center: options.center || ["50%", "50%"],
            //玫瑰饼图配置项
            roseType: options.roseType ? options.roseType : false, //area,radius
            radius: options.radius ? options.radius : ["0%", "60%"],
            label: {
              show: true,
              position: "outside",
              fontSize: options.labelFontSize ? options.labelFontSize : 16,
              align: "center",
              color: "#fff"
              // formatter: '{text|{b}}',
              // rich: rich
            },
            labelLine: {
              show: true, //是否显示引导线
              // length: 15,
              // length2: 30,
              lineStyle: {
                color: this.$STANDARD.labelLineColor
              }
            },
            itemStyle: {
              color: (params) => {
                return Array.isArray(options.colors[params.dataIndex])
                  ?
                  {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [{
                      offset: 0, color: options.colors[params.dataIndex][0] // 0% 处的颜色
                    }, {
                      offset: 1, color: options.colors[params.dataIndex][1]// 100% 处的颜色
                    }],
                    global: false // 缺省为 false
                  }
                  :
                  options.colors[params.dataIndex]
              },
            },
            data: fixData,
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              // 越往后的数据延迟越大
              return idx * 80;
            }
          },
          // 占位扇区，为了扇区内部能呈现百分比，而扇区外部能有标签
          seriesBG = {
            name: "内部扇区",
            type: "pie",
            center: options.center || ["50%", "50%"],
            //玫瑰饼图配置项
            roseType: options.roseType ? options.roseType : "", //area,radius
            radius: options.radius ? options.radius : ["0%", "60%"],
            itemStyle: {
              color: (params) => {
                return Array.isArray(options.colors[params.dataIndex])
                  ?
                  {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [{
                      offset: 0, color: options.colors[params.dataIndex][0] // 0% 处的颜色
                    }, {
                      offset: 1, color: options.colors[params.dataIndex][1]// 100% 处的颜色
                    }],
                    global: false // 缺省为 false
                  }
                  :
                  options.colors[params.dataIndex]
              },
            },
            label: {
              normal: {
                show: false,
                position: "center",
                // fontSize: options.labelInnerSize ? options.labelInnerSize : 16,
                formatter: '{percent|{d}%}',
                // formatter: [
                //   '{value|{c}}',
                //   '{percent|{d}%}'
                // ].join('\n'),
                rich: rich
              },
              emphasis: {
                show: true
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: fixData,
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              // 越往后的数据延迟越大
              return idx * 80;
            }
          },
          // 根据是否要有内部百分比来生成最后series
          seriesObj = options.insidePercent
            ? [seriesBG, seriesItem]
            : [seriesItem];

        //设置饼图的配置项
        this.myChart.setOption({
          tooltip: {
            show: options.tooltip
              ? options.tooltip.show === undefined
                ? true
                : options.tooltip.show
              : true,
            trigger: "item",
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            textStyle: {
              color: this.$STANDARD.tooltipColor
            },
            position: options.labelShow === false ? ["33%", "35%"] : null,
            formatter: function (params) {
              return params.name
                ? `${params.marker}${params.name} : ${params.value}`
                : "";
            }
          },
          title: {
            show: options.titleShow ? true : false,
            text: "{total|" + total + "}" + "\n总数",
            left: "center",
            top: "center",
            padding: [12, 0],
            textStyle: {
              color: this.$STANDARD.titleColor,
              // fontSize: this.$STANDARD.h3.fontSize,
              align: "center",
              rich: rich
            }
          },
          legend: {
            show: options.legend ? options.legend.show : false,
            left: options.legend
              ? options.legend.left
                ? options.legend.left
                : "left"
              : "left",
            top: options.legend
              ? options.legend.top
                ? options.legend.top
                : "top"
              : "top",
            textStyle: {
              // fontSize: this.$STANDARD.h4.fontSize,
              color: this.$STANDARD.legendColor
            },

            orient: options.legend
              ? options.legend.orient
                ? options.legend.orient
                : "vertical"
              : "vertical"
          },
          series: seriesObj
        });

        let dataIndex = -1;
        this.myChart.on("click", params => {
          let name = '';

          // 取消之前高亮的图形
          var dataLen = chartData.length;
          for (let i = 0; i < dataLen; i++) {
            // if (i != params.dataIndex) {
            this.myChart.dispatchAction({
              type: "downplay",
              seriesIndex: 0,
              dataIndex: i
            });
            // }
          }
          // 判断是否重复点击
          if (dataIndex === params.dataIndex) {
            dataIndex = -1;
            name = '';
          } else {
            dataIndex = params.dataIndex;
            // 高亮当前图形
            this.myChart.dispatchAction({
              type: "highlight",
              seriesIndex: 0,
              dataIndex: params.dataIndex
            });
            name = params.name;
          }
          this.$emit("change", name);
        });

        if (this.params.options.rollFlag) {
          this.chartMove();
          this.myChart.on("mouseover", params => {
            // 鼠标移到内部圆环上时不触发事件
            if (params.seriesIndex === 0) return;
            this.params.options.rollFlag = false;
            if (params.dataIndex === this.app.currentIndex) {
              // 序号相同不取消高亮
              return;
            } else {
              // 取消之前高亮的图形
              this.myChart.dispatchAction({
                type: "downplay",
                seriesIndex: 0,
                dataIndex: this.app.currentIndex
              });
            }
          });
          this.myChart.on("mouseout", params => {
            this.params.options.rollFlag = true;
          });
        } else {
          let defaultHigh = this.params.options.defaultHigh;
          if (defaultHigh !== "" && defaultHigh !== undefined) {
            this.myChart.dispatchAction({
              type: "highlight",
              seriesIndex: 0,
              dataIndex: defaultHigh
            });
            // let _this = this;
            // _this.myChart.on("mouseover", function (params) {
            //   var dataLen = chartData.length;
            //   for (var i = 0; i < dataLen; i++) {
            //     if (i != params.dataIndex) {
            //       _this.myChart.dispatchAction({
            //         type: "downplay",
            //         seriesIndex: 0,
            //         dataIndex: i
            //       });
            //     }
            //   }
            // });
            // _this.myChart.on("mouseout", function (params) {
            //   _this.myChart.dispatchAction({
            //     type: "highlight",
            //     seriesIndex: 0,
            //     dataIndex: defaultHigh
            //   });
            // });
          }
        }
      },
      // 控制tootile 的自动轮播
      chartMove() {
        this.app = {};
        this.app.currentIndex = -1;
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.interval = setInterval(() => {
          let dataLen = this.params.data.length;
          if (this.params.options.rollFlag) {
            // 取消之前高亮的图形
            this.myChart.dispatchAction({
              type: "downplay",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
            this.app.currentIndex = (this.app.currentIndex + 1) % dataLen;
            // 高亮当前图形
            this.myChart.dispatchAction({
              type: "highlight",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
            this.myChart.dispatchAction({
              type: "showTip",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
          }
        }, 2000);
      },
    },
    computed: {
      canvasStyle() {
        return {
          width: this.params.canvasStyle
            ? this.params.canvasStyle.width
            : "500px",
          height: this.params.canvasStyle
            ? this.params.canvasStyle.height
            : "500px"
        };
      },
    },
    watch: {
      animateFlag: {
        handler(newVal, oldVal) {
          this.initChart();
        }
      },
      'params.data': {
        handler(newVal, oldVal) {
          this.initChart();
        },
        deep: true
      }
    }
  };
</script>

<style scoped>
</style>
