<template>
  <div class="btn3">
    <ul>
      <li
        v-for="(item,index) of arr"
        :key="index"
        :class="curName===item?'active':''"
        @click="handleClick(item)"
      >
        <i class="icon"></i>
        {{item}}
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "btn3",
    data() {
      return {
        arr: ["问题属地", "常住地"],//"户籍地"
        curName: "invalid"
      }
    },
    methods: {
      handleClick(name) {
        this.curName = name;
        this.$emit("change", this.curName);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .btn3
    position relative
    width 100px
    ul
      li
        position relative
        padding-left 25px
        color #cbfbff
        line-height 30px
        font-size 0.14rem
        display flex
        cursor pointer
        .icon
          position absolute
          top 9px
          left 2px
          width 8px
          height 8px
          display inline-block
          border-radius 50%
          border 2px solid #cbfbff
        &.active
          color #fff15b
          .icon
            top 7px
            left 0
            width 12px
            height 12px
            border-radius 50%
            border 2px solid #fff15b
            &:after
              content ''
              position absolute
              top 2px
              left 2px
              width 8px
              height 8px
              border-radius 50%
              background-color #fff15b
</style>
