<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-left">
        <ctitle :title="'一级内容信访量占比'"></ctitle>
        <div class="inner">
          <div class="c-l-t">
            <allCount class="part" :count="allcount"></allCount>
            <detailList class="part" :params="detailList"></detailList>
          </div>
          <div class="c-l-b">
            <!--<pie-chart3D :params="pieParams" @canvasClick="handlePieClick"></pie-chart3D>-->
            <BasicPie :params="pieParams" @change="handlePieClick"></BasicPie>
          </div>
        </div>
      </div>
      <div class="c-right">
        <ctitle :title="`${curName}信访量分布`"></ctitle>
        <div class="inner">
          <btn1 class="btn1" @change="btn1Click"></btn1>
          <btn2 class="btn2" @change="btn2Click"></btn2>
          <btn3 class="btn3" v-show="btn3Show" @change="btn3Click"></btn3>
          <div v-if="mapParams.options.JSON!=='hebei'" class="map-back" @click="mapBack"></div>
          <Map class="map" :params="mapParams" @change="handleMap"></Map>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";
  import PieChart3D from "@/components/pie/PieChart3D";
  import Map from "@/components/map/Map";
  import BasicPie from "@/components/pie/BasicPie";
  import btn1 from "@/components/btnChange/btn1";
  import btn2 from "@/components/btnChange/btn2";
  import btn3 from "@/components/btnChange/btn3";

  export default {
    name: "dyfb",
    components: {
      ctitle,
      allCount,
      detailList,
      PieChart3D,
      Map,
      BasicPie,
      btn1,
      btn2,
      btn3
    },
    data() {
      return {
        curName:"",
        curCity: "河北省",
        btn3Show: false,//按钮2：件次（按钮3不显示）、人次（按钮3显示）
        mapJson: {
          "河北省": "hebei",
          "石家庄市": "shijiazhuang",
          "保定市": "baoding",
          "唐山市": "tangshan",
          "秦皇岛市": "qinhuangdao",
          "邯郸市": "handan",
          "邢台市": "xingtai",
          "张家口市": "zhangjiakou",
          "承德市": "chengde",
          "沧州市": "cangzhou",
          "廊坊市": "langfang",
          "衡水市": "hengshui",
          "定州市": "dingzhou",
          "辛集市": "xinji",
          "雄安新区": "xionganxinqu",
        },
        // 信访量占比
        allcount: -1,
        detailList: null,
        pieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            defaultHigh:-1,
            colors:this.$STANDARD.pieMiddleColors,
            title: "",
            insidePercent:true,
            radius: ["35%", "60%"],
            center: ["50%", "50%"],
            labelShow:true,
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.24),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            borderColor: "#0B2244",
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 0,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "20%", // label字号
            rollFlag: false
          }
        },
        //地图
        mapParams: {
          canvasStyle: {
            width: "90%",
            height: "100%",
          },
          data: [],
          options: {
            mockData: false,
            JSON: "hebei",
            xName: "name",
            yName: "value",
            map: {
              "value": "信访量"
            },
            carousel: true
          }
        },
      }
    },
    mounted() {
      this.getAPI(this.year);
    },
    methods: {
      getAPI(year) {
        // 1.占比（总数）
        get_http("gjzlSdgjDyfbZbTotal", {year: year})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.占比（各类型）
        get_http("gjzlSdgjDyfbZbList", {year: year})
          .then(res => {
            this.detailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.占比（饼图）
        get_http("gjzlSdgjDyfbZbPie", {year: year})
          .then(res => {
            this.pieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4地图
        this.getMapAPI();
      },
      getMapAPI(year) {
        get_http("gjzlSdgjDyfbFbMap", {year: year})
          .then(res => {
            this.mapParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击左侧饼图联动右侧地图
      handlePieClick(name) {
        this.curName = name;
        this.getMapAPI();
      },
      // 点击地图下钻
      handleMap(name) {
        this.curCity = name;
        for (let key in this.mapJson) {
          if (this.curCity === key) {
            this.mapParams.options.JSON = this.mapJson[name];
          }
        }
      },
      // 地图返回全省
      mapBack() {
        this.curCity = "河北省";
        this.mapParams.options.JSON = "hebei";
      },
      btn1Click(name) {
        this.getMapAPI();
      },
      btn2Click(name) {
        this.btn3Show = !(name === '件次');
        this.getMapAPI();
      },
      btn3Click(name) {
        this.getMapAPI();
      },
      fontSize(res) {
        let clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val, this.curTypeId);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box
      .inner
        width 100%
        height calc(100% - 0.28rem)
      .c-left
        flex 1
        border-right 1px #91e3f7 dashed
        .inner
          display flex
          flex-direction column
          .c-l-t
            flex 1
            display flex
            justify-content space-around
            align-items center
            .part
              flex 1
          .c-l-b
            flex 1.4
            height 100%
      .c-right
        flex 1
        .inner
          position relative
          height calc(100% - 0.28rem)
          background url("../../../assets/map-bg.png") no-repeat 0 bottom
          .btn1
            position absolute
            top -50px
            right 74px
            z-index 100
          .btn2
            position absolute
            top 50px
            right 80px
            z-index 100
          .btn3
            position absolute
            top 170px
            right 70px
            z-index 100
          .map-back
            position absolute
            top 30px
            left 60px
            z-index 1000
            width 30px
            height 30px
            background url("../../../assets/back-icon.png") no-repeat
            /*font-size 0.2rem*/
            /*color #fff*/
            cursor pointer
          .map
            position relative
            top -40px
            left 0
</style>
